#dashboardHeaderLeft{
    padding: 24px 32px;
}

#dashboardHeaderRight {

}

#dashboardHeaderLeft, #dashboardHeaderRight {
    background-color: #fff;
    box-shadow: 0px 0px 6px rgba(120, 120, 120, 0.25);
    border-radius: 12px;
}

#DashboardHeader {
    gap: 15px;
}

#DashboardHeader h1 {
    font-size: 20px;
    font-weight: 500;
}

#DashboardHeader p {
    font-size: 14px;
    font-weight: 400;
    margin-top: 2px;
}

.sectionSubHead p {
    margin-bottom: 27px;
    color: #787878;
}

#btnOrange {
    padding: 8px 12px;
    background-color: #FF7714;
    color: white;
    border-radius: 4px;
}

#walletColumns h5 {
    font-size: 14px;
    font-weight: 600;
}
#walletColumns h6 {
    font-size: 14px;
    font-weight: 500;
    color: #737373;
}


.pie {
    left: 0;
    right: 0;
    margin: auto;
}
.pie svg {
    left: 0;
    right: 0;
    margin: auto;
}

.statText h3 {
    font-size: 18px;
    font-weight: 700;
    line-height: 23px;
    letter-spacing: 0em;
}
.statText h6 {
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0em;
    color: #595959;
    margin-top: 8px;
}

#dashboardHeaderRight{
    padding: 14px;
}

#dashboardHeaderRight h1 {
    font-size: 16px;
    font-weight: 500;
    line-height: 21px;
    color: #595959;
    
}

#dashboardHeaderRight h2 {
    font-size: 12px;
    font-weight: 400;
    line-height: 21px;
    color: #595959;
    
}