#merchantRight {
    font-family: "Poppins", sans-serif;
}

.status {
    background-color: #fff;
    padding-top: 8px;
    padding-left: 12px;
    padding-bottom: 16px;
    border-radius: 4px;
    box-shadow: 0px 0px 6px rgba(120, 120, 120, 0.25);
}

.status h5 {
    font-size: 13px;
    font-weight: 500;
}

#statusContainer {
    gap: 50px;
}

.status img {
    width: 40px;
    height: 40px;
}

.status h1 {
    color: #0E8AFF;
    font-weight: 700;
    font-size: 32px;
    margin-left: 21px;
}

#merchantRight .arrow {
    width: 12px;
    height: 17px;
    margin-left: 7.5px;
}

.statusText {
    margin-top: 23px;
}

#productivityContainer {
    margin-top: 33px;
    gap: 50px;
}

.stat {
    color: white;
}

#newOrder {
    background: #EC3B83;
}

#newSales {
    background: #F56854;
}

#newUsers {
    background: #00B19D;
}

#feedback {
    background: #00C0EF;
}

.stat {
    border-radius: 12px;
}

.stat h5 {
    margin: 16px;
    font-size: 16px;
    font-weight: 500;
}
.stat h1 {
    font-size: 24px;
    font-weight: 500;
    padding-left: 16px;
    padding-bottom: 29px;
}

.desc {
    margin-top: 12px;
    margin-right: 12px;
    margin-bottom: 16px;
    text-align: right;
}


#transactionAnalytics {
    margin-top: 34px;
    gap: 21px;
}

#transactionAnalytics circle {
    display: none;
}

#monthDetils {
    background-color: #fff;
    padding: 16px;
    box-shadow: 0px 0px 6px rgba(120, 120, 120, 0.25);
    border-radius: 12px;
    padding-bottom: 38px;
}

#monthDetailsIndiciator{
    gap: 12px;
}

.circle {
    width: 12px;
    height: 12px;
    margin-right: 4px;
}

#online {
    color: #00B063;
}

#online .circle {
    background-color: #00B063;
    border-radius: 50%;
}

#upi {
    color: #14C9C9;
}

#upi .circle {
    background-color: #14C9C9;
    border-radius: 50%;
}

#statistics {
    background-color: #fff;
    box-shadow: 0px 0px 6px rgba(120, 120, 120, 0.25);
    border-radius: 12px;
    padding: 23px 26px;
}

#statistics h2 {
    font-size: 14px;
    font-weight: 500;
    color: #595959;
}

#statImg {
    margin-top: 46px;
}
#statImg img{
    margin-bottom: 17px;
}

.statText {
    margin-top: 49px;
    text-align: center;
}

.statText h1 {
    font-size: 18px;
    font-weight: 700;
}

.statText h3 {
    font-size: 12px;
    font-weight: 400;
    padding-bottom: 50px;
}

#transacHeader {
    margin-bottom: 35px;
}

#invoice {
    gap: 24px;
    margin-top: 36px;
}

#tableContainer {
    border: 1px solid #D9D9D9;
    border-radius: 12px !important;
}


#tfoot {
    color: #595959;
}

#tFoot button{
    padding: 5px 10px;
    background-color: #d9d9d9;
    margin-right: 8px;
    border-radius: 5px;
    font-size: 14px;
    font-weight: 400;
}

#transactionGrowthChart {
    background-image: linear-gradient(337.07deg, #435AF4 0%, rgba(118, 56, 181, 0.67) 100%);
    border-radius: 12px;
    padding: 16px;
    color: white;
}

.completed {
    padding: 4px 10px;
    background-color: #209D50;
    font-size: 14px;
    font-weight: 600;
    color: #f5f5f5;
    border-radius: 8px;
}
.delay {
    padding: 4px 10px;
    background-color: #FF620E;
    font-size: 14px;
    font-weight: 600;
    color: #f5f5f5;
    border-radius: 8px;
}
.failed {
    padding: 4px 10px;
    background-color: #FF0E0E;
    font-size: 14px;
    font-weight: 600;
    color: #f5f5f5;
    border-radius: 8px;
}

#transactionGrowthChart h4 {
    font-weight: 500;
    font-size: 14px;
}
#transactionGrowthChart h1 {
    font-weight: 700;
    font-size: 32px;
    margin-top: 24px;
}

#transactionGrowthChart h3 {
    font-weight: 500;
    font-size: 16px;
    margin-top: 3px;
}
#transactionGrowthChart p {
    font-weight: 500;
    font-size: 16px;
    margin-top: 3px;
    padding: 31px;
}

#summary {
    gap: 23px;
    margin-top: 36px;
    margin-bottom: 88px;
}

.summaryItem {
    background: #FFFFFF;
    box-shadow: 0px 0px 6px rgba(120, 120, 120, 0.25);
    border-radius: 12px;
    overflow: hidden;
}

#profile {
    background-color: #F39C11;
    padding: 16px;
    color: white;
}

#profile img {
    width: 72px;
}

#transactionHistory h6 {
    font-weight: 500;
    font-size: 16px;
}

#transactionHistoryText{
    padding: 16px;
}

#transactionHistoryText li {
    padding: 16px 0;
}

#transactionHistoryText button {
    padding: 5px 10px;
    border-radius: 4px;
    color: white;
}

#daily {
    background-color: #1fba86;
}

#weekly {
    background-color: #1F82BA;
}

#monthly {
    background-color: #511FBA;
}

#ninetyth {
    background-color: #BA1F8F;
}

#halfYearly {
    background-color: #BA981F;
}

#yearly {
    background-color: #BA3B1F;
}

#details {
    color: #0E8AFF;
    font-size: 16px;
    font-weight: 500;
}

#recentFeedback {
    padding: 16px 24px;
}

#recentFeedback h3, #recentTransaction h3 {
    font-size: 16px;
    font-weight: 500;
    color: #595959;
}

#recentFeedback a, #recentTransaction a {
    color: #0E8AFF;
    font-size: 16px;
    font-weight: 500;
}

.feedback {
    background: #E1F0FE;
    border-radius: 12px;
    padding: 16px 10px;
    font-size: 16px;
    font-weight: 500;
}

.feedBacksContainer {
    gap: 12px;
    margin-top: 32px;
}

#recentTransaction {
    padding: 16px 24px;
}

.transaction {
    margin: 21px 0;
}

.transacCircle {
    width: 15px !important;
    height: 14px !important;
    margin: 6px !important;
    border-radius: 50%;
}

.transaction h3 {
    font-size: 16px;
    font-weight: 500;
}

.transactionPara {
    padding: 8px 0;
}

#recentTransactionContainer {
    background-image: url("./../../Assets/images/transaction-bg.png");
    background-repeat: no-repeat;
    background-position-x: 11px;
    background-position-y: 7px;
}

#merchantFooter {
    background-color: #fff;
    padding: 24px 41px;
    font-size: 15px;
    font-weight: 500;
}