}

.btnOrange {
    background-color: #FF5E14;
    padding: 4px 15px;
    color: white;
    border-radius: 4px;
}

.btnDark {
    background: #D9D9D9;
    border-radius: 4px;
    padding: 6px 14px;
    display: flex;
    align-items: center;
}

#icons {
    margin-top: 22px;
}

#transactionRight input {
    background: #F0F0F0;
    border: none !important;
    padding: 4px 14px;

    border-radius: 4px;
    width: 163px;
}

.btnBlue {
    background-color: #1645EC;
    padding: 4px;
    border-radius: 4px;
