@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

#right {
  background: #fafafa;
}

.recharts-wrapper {
  left: -8px;
  bottom: -18px;
  overflow: hidden;
  z-index: 0;
}

#pie-chart {
  position: relative;
}

.recharts-cartesian-grid-vertical {
  display: none;
}

#pie-chart .recharts-surface {
  width: 195px;
}

.tab.tab-active {
  border-bottom: 1.5px solid #FF620E;
  font-weight: 600;
  font-size: 14px;
  color: #000;
}

.tab-bordered {
  border-bottom: none;
  color: #737373;
  font-weight: 500;
  font-size: 14px;
}