#dashboardStat {
    gap: 20px;
    padding: 25px;
    box-shadow: 0px 1px 3px rgba(183, 183, 183, 0.42);
    margin-top: 15px;
    background-color: #fff;
    border-radius: 12px;
}

.revenue {
    background-color: #DEF1FF;
    padding: 12px;
    border-radius: 8px;
    overflow: hidden;
}
.pageview {
    background-color: #FEF7DA;
    padding: 12px;
    border-radius: 8px;
    overflow: hidden;
}
.bounceRate {
    background-color: #E0FFD2;
    padding: 12px;
    border-radius: 8px;
    overflow: hidden;
}
.total {
    background-color: #FFE7DD;
    padding: 12px;
    border-radius: 8px;
    overflow: hidden;
}

#dashboardStat h1 {
    font-size: 32px;
    font-weight: 700;
    font-family: "Poppins", sans-serif;
}

#dashboardStat circle {
    display: none;
}

#dashboardStat .recharts-area {
    width: 100%;
}

#dashboardStat .recharts-wrapper {
    left: -13px;
}